
import React, { Component } from "react";
import  { Link } from "react-router-dom";
import http from "./httpService.js"

class NewPurchases extends Component {
state={
    purchases: {shopid:"", productid:"",quantity:"",price:""}
   
    
}
async fetchData(){

       let purchases= {shopid:"", productid:"",quantity:"",price:""}
       this.setState({ purchases:purchases})
    
}

async componentDidMount(){
    this.fetchData();
}

componentDidUpdate(prevProps,prevState){
    if(prevProps!==this.props) this.fetchData();
}

handleChange=(e)=>{

    const {currentTarget:input}=e
    let s1={...this.state}
   s1.purchases[input.name]=input.value
    this.setState(s1)
}
async postData(url, obj){
    let response=await http.post(url, obj);
    console.log(response);
    this.props.history.push("/purchases");

}

handleSubmit=(e)=>{
    e.preventDefault();
    let {purchases,edit}=this.state;
   
  this.postData("/purchases", purchases)

};

render(){
    let {purchases}=this.state;
 
  let {shopid, productid,quantity,price}=purchases;
    return(
    <div className="container">
       <h2 className="text-center">Add purchases</h2>
     <div className="form-group">
         <label> shopId</label>
         <input 
         type="text"
         className="form-control"
         id="shopid"
         name="shopid"       
         value={shopid}
         placeholder="Enter shopid"
         onChange={this.handleChange}
          />
     </div><br/>
     <div className="form-group">
         <label> ProdId</label>
         <input 
         type="text"
         className="form-control"
         id="productid"
         name="productid"       
         value={productid}
        placeholder="Enter productid"
         onChange={this.handleChange}
          />
     </div><br/>
     <div className="form-group">
         <label> Quantity</label>
         <input 
         type="text"
         className="form-control"
         id="quantity"
         name="quantity"       
         value={quantity}
         placeholder="Enter quantity"
         onChange={this.handleChange}
          />
     </div><br/>
     <div className="form-group">
         <label>price</label>
         <input 
         type="text"
         className="form-control"
         id="price"
         name="price"       
         value={price}
        placeholder="Enter price"
         onChange={this.handleChange}
          />
     </div><br/>
    
     <button className="btn btn-primary m-2" onClick={this.handleSubmit}>Submit</button>
                </div>
    )
}
}
export default NewPurchases;


