import React, { Component } from "react";

class Leftpanal extends Component {
    state={
      
    shopOP:["1","2", "3", "4"],
   sortOP:["QtyAsc","QtyDesc","ValueAsc","ValueDesc"]
       
    }
    handleChange=(e)=>{ 
        const {currentTarget:input}=e;
        let options={...this.props.options};
        if(input.name=="shop"){
            options[input.name]="st"+input.value
        }
        else{
            options[input.name]=input.value
        }
           
       console.log(typeof(input.value," input.value"))

   this.props.onOptionChange(options);

     
   
   }

   /*  if(input.name=="product"){
        options[input.name]=this.updateCBs(
            options[input.name]
            ,input.checked,
            input.value
            );
        }
        else{
            */
   updateCBs=(inpValue,checked,value)=>{
      
    let inpArr=inpValue?
    inpValue?.split(",")||[]
    :[];
   

    if(checked) {
        inpArr.push(value)
    }
    else{
        let index=inpArr.findIndex((ele)=>ele===value);
        if(index>=0) inpArr.splice(index,1);
    }
    return inpArr.join(",")


    }

    makeDropdown=(arr,value,name,label)=>(
        <div className="form-group">
        
        <select className="form-control" name={name} value={value}
             onChange={this.handleChange}>
            <option  value=""
            >
           
               choose shop
            </option>
            {arr.map((c1)=>(
                <option>{c1}</option>))}
        </select>
    </div>
    
    );
    makeCheckbox=(arr,values,name)=>(
        <React.Fragment>
        <label className="form-check-label font=weight-bold"></label>
             {arr.map((opt)=>(
                <div className="form-check"  key={opt}>
                    <input className="form-check-input"
                    type="checkbox"
                    name={name}
                    value={"pr"+opt} 
                    checked={"pr"+opt==values ||false}
                      onChange={this.handleChange}
                      />
       <label className="form-check-label ">{opt}</label>
    
          </div>
             ))}
      </React.Fragment>
       );
    render(){
let {shopOP,sortOP}=this.state;
let {OP,OP1}=this.props;
let {shop,product,sort}=this.props.options;
shop=shop?shop.substring(2,3):"";
        return(
            <div className="row">
<div className="col-8 border  bg-light text-bold "  style={{height:"50px"}}>Products</div>
               <div className="col-8 border">
               {this.makeCheckbox(OP,product,"product")}
                </div>
                <div className="col-8 border  bg-light text-bold "  style={{height:"50px"}}>shops</div>
               <div className="col-8 border">
               {this.makeDropdown(OP1,shop,"shop")}
                </div>
                <div className="col-8 border  bg-light text-bold "  style={{height:"50px"}}>sortBy</div>
               <div className="col-8 border">
               {this.makeDropdown(sortOP,sort,"sort")}
                </div>
                </div>

        )
    }


    
}export default Leftpanal;//{this.makeCheckbox(PayOP,product?.split(",")||[],"product")}