import React, { Component } from "react";
import queryString from "query-string";
import  { Link } from "react-router-dom";
import http from "./httpService.js";


class ShopView extends Component {

    state={
        data:[]
    }
    async componentDidMount(){
        let response=await http.get("/shops");
        console.log(response);
        this.setState({data:response.data})
    }
    render(){
    let  {data}=this.state;
return(
    <div className="container">
    <h3 className="text-center"> Shops Details</h3>
    <div className="row">
            {data.map((c1)=>
             
       
            <div className="col-3 bg-warning border text-center">
           <h4 style={{marginTop:"20px"}}>ShopId : {c1.shopid}</h4>
           <h5>Name: {c1.name}</h5><br/>
           <h5>Rent
 : {c1.rent
}</h5><br/>
           <Link to={`/shops/purcheses/${c1.shopid}`}>
               Purchases</Link>   <Link to={`/totalpurcheses/shop/${c1.shopid}`}>
                   Total Purchases</Link>
            </div>
    )}
       </div></div>
)

    }
}export default ShopView;
