
import React, { Component } from "react";
import  { Link } from "react-router-dom";
import http from "./httpService.js"

class NewProducts extends Component {
state={
    product:{productName:"", category:"", description:""},
    edit:false,
}
async fetchData(){

    const {productid}=this.props.match.params;
 if(productid){ 
        let response=await http.get(`/products/${productid}`);
        let {data}=response;
       this.setState({product:data, edit:true })
    }

    else {
       let product= {productName:"", category: "", description: ""}
       this.setState({ product:product, edit:false })
    }
}
async componentDidMount(){
    this.fetchData();
}

componentDidUpdate(prevProps,prevState){
    if(prevProps!==this.props) this.fetchData();
}

handleChange=(e)=>{

    const {currentTarget:input}=e
    let s1={...this.state}

    s1.product[input.name]=input.value
    this.setState(s1)
}

async postData(url, obj){
    let response=await http.post(url, obj);
    console.log(response);
    this.props.history.push("/products");

}
async putData(url, obj){
    let response=await http.put(url, obj);
    console.log(response);
    this.props.history.push("/products");

}
handleSubmit=(e)=>{
    e.preventDefault();
    const {productid}=this.props.match.params;
    let {product,edit}=this.state;
   
    edit
    ?this.putData(`/products/${productid}`,product)
    :this.postData("/products", product)

};

render(){
    let {product,edit}=this.state;
  let {productName, category, description}=product;
  
    return(
    <div className="container">
        <h2 className="text-center">{edit==true?"Edit Product":"Add Product"}</h2>
        
      {edit===true?
     <div className="form-group">
         <label>Product Name</label>
         <input 
         type="text"
         className="form-control"
         id="productName"
         name="productName"       
         value={productName}
        placeholder="Enter name"
        onChange={this.handleChange}
        readOnly
          />
     </div>
     : <div className="form-group">
     <label>Product Name</label>
     <input 
     type="text"
     className="form-control"
     id="productName"
     name="productName"       
     value={productName}
    placeholder="Enter name"
    onChange={this.handleChange}
    
      />
 </div>}
     <div className="form-group">
         <label>Product category</label>
         <input 
         type="text"
         className="form-control"
         id="category"
         name="category"       
         value={category}
        placeholder="Enter category"
         onChange={this.handleChange}
          />
     </div><br/>
     <div className="form-group">
         <label>Description</label>
         <input 
         type="text"
         className="form-control"
         id="description"
         name="description"       
         value={description}
        placeholder="Enter description"
         onChange={this.handleChange}
          />
     </div><br/>
     <button className="btn btn-primary m-2" onClick={this.handleSubmit}>Submit</button>
                </div>
    )
}
}
export default NewProducts;


