import React, { Component } from "react";
import  {  Redirect, Route, Switch } from "react-router-dom";

import NavBar from "./navBar";
import ProductsView from "./productsView";
import NewProducts from "./newProducts";
import ProductPurches from "./productPurches";
import TotalPurchs from "./totalpurchs"
import AllPurcheses from "./allPurcheses";
import ShopView from "./shopView";
import ShopPurcheses from "./shopPurcheses";
import TotalPurchesesShop from "./totalPurseseShop";
import AddShop from "./addShop";
import NewPurchases from "./newPurchases";

class MainComponent extends Component {
    render(){
       
        return(
            <div className="container">
                <NavBar />
                <Switch>

                <Route path="/products/:productid/edit" component={NewProducts}/> 
               
                <Route path="/totalpurcheses/shop/:id" component={TotalPurchesesShop}/>
            <Route path="/totalpurcheses/:id" component={TotalPurchs}/>
            <Route path="/shops/purcheses/:id" component={ShopPurcheses}/>
            <Route path="/products/purcheses/:id" component={ProductPurches}/>
         
            <Route path="/shops/Add" component={AddShop}/>
            <Route path="/products/Add" component={NewProducts}/>
            <Route path="/shops/View" component={ShopView}/>
            <Route path="/products/View" component={ProductsView}/>
            <Route path="/new/purchase" component={NewPurchases}/>
            <Route path="/purchases" component={AllPurcheses}/>
          
                  </Switch>
               
            </div>
        )

    }

}export default MainComponent;

