import React, { Component } from "react";
import queryString from "query-string";
import  { Link } from "react-router-dom";
import http from "./httpService.js";


class ShopPurcheses extends Component {

    state={
        data:[],
    }
    async componentDidMount(){
        let {id}= this.props.match.params;
        let response=await http.get(`/purchases/shops/${id}`);
        console.log(response);
        this.setState({data:response.data})
    }
    render(){
    let  {data}=this.state;
return(
    <div className="container">
    <h3 className="text-center">Purcheses</h3>
    <div className="row">
            {data.map((c1)=>
             
       
            <div className="col-3 bg-warning border text-center">
           <h4 style={{marginTop:"20px"}}>purchaseId : {c1.purchaseid}</h4><br/>
           <h5>Name : {c1.name}</h5><br/>
           <h5>Rent : {c1.rent}</h5><br/>
           <h5>quantity : {c1.quantity}</h5><br/>
           <h5>price : {c1.price}</h5><br/>

           
            </div>
           
    )}
       </div></div>
)

    }
}export default ShopPurcheses ;
