import React, { Component } from "react";
import queryString from "query-string";
import  { Link } from "react-router-dom";
import http from "./httpService.js";

import Filter from "./filter.jsx";

class AllPurcheses extends Component {

    state={
        data:[],
    }
    async fetchData(){
        let queryParams=queryString.parse(this.props.location.search);
       
        let searchStr= this.makeSearchString(queryParams)
        let response=await http.get(`/purchases?${searchStr}`);
        console.log(response);
        
        this.setState({data:response.data})
    }

    componentDidMount (){
     
        this.fetchData();
    }
    componentDidUpdate(prevProps,prevState){
        if(prevProps!==this.props) this.fetchData();
    }
    makeSearchString=(options)=>{
      
       
        let {product,shop,sort}=options;
            let searchStr="";
         searchStr=this.addToQueryString(searchStr,"product",product);
         searchStr=this.addToQueryString(searchStr,"shop",shop);
        searchStr=this.addToQueryString(searchStr,"sort",sort);
         
         return searchStr;
      };
         addToQueryString = (str,paramName,paramValue) =>
         paramValue
         ? str 
         ? `${str}&${paramName}=${paramValue}`
         :`${paramName}=${paramValue}`
         :str;
 
         
     
      callURL=(options)=>{
         let searchString=this.makeSearchString(options);
         this.props.history.push({
          search:searchString,
         });
        
      }
      handleOPtionChange=(options)=>{
       
         this.callURL(options)
     }

     handlecity=(e)=>{ 
        const {currentTarget:input}=e;
    let queryParams=queryString.parse(this.props.location.search);
    queryParams.sortBy=input.value
    console.log("val",input.value)
    this.callURL(queryParams);

   }
    render(){
        let queryParams=queryString.parse(this.props.location.search);
        let searchStr= this.makeSearchString(queryParams)
        const {data}=this.state;

        let OP = data.reduce(function (acc, curr) {
            if (!acc.includes(curr.productid))
                acc.push(curr.productid);
                return acc;
        }, []);
        let OP1 = data.reduce(function (acc, curr) {
            if (!acc.includes(curr.shopid))
                acc.push(curr.shopid);
                return acc;
        }, []);
      
        return(
<div className="container">
<div className="row ">
                 <div className="col-3">
             
                 <Filter
                 OP={OP}
                 OP1={OP1}
               options={queryParams}
               onOptionChange={this.handleOPtionChange}
               />
                </div>
                    <div className="col-9">



                    <div className="row border bg-dark text-white">
        <div className="col-2 border">
        purchaseId
            </div>
            <div className="col-2 border">
            shopId
            </div>
            <div className="col-2 border " >
            productid
            </div>
           
            <div className="col-3 border" >
            quantity
            </div>
            <div className="col-3 border">
            price
            </div>
          
            </div>
{data.map((st)=>
    <div className="row border">
        <div className="col-2 border">
      {st.purchaseId}
            </div>
            <div className="col-2 border">
            {st.shopId}
            </div>
            <div className="col-2 border">
            {st.productid}
            </div>
            <div className="col-3 border">
            {st.quantity}
            </div>
            <div className="col-3 border">
            {st.price}
            </div>
           
            </div>)}
          
</div> </div> </div>
        )
    }
}export default AllPurcheses;