import React, { Component } from "react";
import queryString from "query-string";
import  { Link } from "react-router-dom";
import http from "./httpService.js";


class ProductsView extends Component {

    state={
        data:[]
    }
    async componentDidMount(){
        let response=await http.get("/products");
        console.log(response);
        this.setState({data:response.data})
    }
    render(){
    let  {data}=this.state;
return(
    <div className="container">
    <h3 className="text-center"> Products Details</h3>
    <div className="row">
            {data.map((c1)=>
             
       
            <div className="col-3 bg-warning border text-center">
           <h4 style={{marginTop:"20px"}}>productId : {c1.productid}</h4><br/>
           <h5>productName : {c1.productname}</h5><br/>
           <h5>Category : {c1.category}</h5><br/>
           <h5>Description : {c1.description}</h5><br/>
            <Link to={`/products/${c1.productid}/edit`}>edit</Link>   <Link to={`/products/purcheses/${c1.productid}`}>
               Purchases</Link>   <Link to={`/totalpurcheses/${c1.productid}`}>
                   Total Purchases</Link>
            </div>
           
    )}
       </div></div>
)

    }
}export default ProductsView;
